<template>
  <view class="fixed-content" 
        @touchstart.stop="onDragStart"
        @touchmove.stop="onDragMove"
        @touchend.stop="onDragEnd">
    <!-- 拖动条 -->
    <view class="drag-handle">
      <view class="drag-bar"></view>
    </view>
    
    <!-- 插槽内容 -->
    <slot></slot>
  </view>
</template>

<script>
export default {
  methods: {
    onDragStart(e) {
      this.$emit('drag-start', e)
    },
    onDragMove(e) {
      this.$emit('drag-move', e)
    },
    onDragEnd(e) {
      this.$emit('drag-end', e)
    }
  }
}
</script>

<style>
.fixed-content {
  position: relative;
  width: 100%;
  z-index: 10;
  background-color: #fff;
  border-radius: 20px 20px 0 0;
}

.drag-handle {
  height: 40rpx;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drag-bar {
  width: 60rpx;
  height: 6rpx;
  background: #ddd;
  border-radius: 3rpx;
}
</style>